#granola.render { 

/* class names should be "generic" layout, like horizontal and vertical */
/* TODO: how to deal with responsive? */
    .navigation() {
        .g-nav { 
            #granola.nav(outer);
            & ul { #granola.nav(list); }
            & li { #granola.nav(list-item); }
            & a { #granola.nav(list-link); }
            &-justify { #granola.nav(nav-justify); }
            &-justify-item { #granola.nav(nav-justify-item); }
        }
        #granola.sel.prefix("btn-", {
            display: inline-block;
        });
    }
}
